.choose-sort {
  min-width: auto;
  height: 46%;
  border-radius: 182px;
  color: black;
}
.choose-sort__title {
  white-space: nowrap;
  padding-left: 4px;
  margin-right: 7px;
  margin-left: 5px;
}
.choose-sort__delete-choose {
  width: 26px;
  height: 25px;
  border-radius: 50%;
  transition: transform 400ms linear;
  margin-right: 3px;
  margin-left: 5px;
}
.choose-sort__delete-choose--cross {
  line-height: 6px;
  font-size: 9px;
}
.choose-sort__delete-choose:hover {
  transform: rotate(180deg);
}

.dropdown {
  width: 150px;
  height: 38px;
  cursor: pointer;
  position: relative;
}
.dropdown__visible {
  top: 0%;
  left: 0;
  height: 38px;
  position: absolute;
  border: 1px solid #CBCBD9;
}
.dropdown__visible--items-drop {
  width: 9px;
  height: 6px;
  line-height: 5px;
  font-size: 9px;
  color: #6E7585;
}
.dropdown__hidden {
  overflow-y: auto;
  position: absolute;
  height: 165px;
  top: 38px;
  left: 0;
  box-shadow: 0px 11px 20px rgba(29, 29, 47, 0.25);
}
.dropdown__hidden p {
  height: 50px;
  border-bottom: 1px solid #CBCBD9;
  line-height: 50px;
